.container .nav-left {
  height: calc(100vh);
  background: #001529;
}
.container .main {
  height: calc(100vh);
  overflow: auto;
}
.container .content {
  position: relative;
  padding: 20px;
  background-color: #f5f5f5;
}
.header .header-top {
  height: 60px;
  line-height: 60px;
  padding: 0 20px;
  text-align: right;
}
.header .header-top a {
  margin-left: 40px;
}
.header .breadcrumb {
  height: 40px;
  line-height: 40px;
  padding: 0 20px;
  border-top: 2px solid #f9c700;
}
.header .breadcrumb h2 {
  margin: -1px;
}
.header .breadcrumb .breadcrumb-title {
  text-align: center;
}
.header .breadcrumb .breadcrumb-title:after {
  position: absolute;
  content: '';
  left: 90px;
  border-top: 12px solid white;
  border-left: 12px solid #f5f5f5;
  border-right: 12px solid #f5f5f5;
  background-color: red;
  z-index: 100;
}
.header .breadcrumb .tip {
  text-align: right;
}
.header .breadcrumb .tip .weather {
  margin-left: 20px;
}
